body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 15px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.chart-container {
  position: relative;
  margin: auto;
  height: 80vh;
  width: 80vw;
}

.domainCard {
  width: 24% !important;
  margin: 6px !important;
  color: #222 !important;
  border: 3px solid #00C853;
}

.domainCard h4 {
  font-size: 1.1em !important;
}

.nav-drawer {
  width: 250px;
}

.drawerPaper {
  width: 250px;
}

.drawerHeader {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 8px;

}

/* 404 View Styles */
.fof {
  margin: 1em;
  overflow: auto;
  margin-left: 1%;
  width: 98%;
  background-color: #FFFFFF;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.fofTable {
  width: 100%;
  min-width: 700px;
  display: table !important;
  position: unset !important;
}

.fofTable thead {
  font-size: .8em;
}

.fofTable tr {
  text-align: left;
  border-bottom: 1px solid rgba(224, 224, 224, 1);
}

.fofTable th {
  padding: 8px;
  width: 30px;
}

.fofTable td {
  padding: 4px 10px;
  font-size: .9em;

}

h1 a {
  color: #FFFFFF !important;
}